<template>
<div class="col-12 col-md-4">
  <div class="card-top-level">
    <!-- <div v-if="imageName">
      <img :src="require('../assets/' + imageName)" class="card-image"/>
    </div>
    <div v-else>
      <div class="card-image"></div>
    </div> -->
    <div class="menu-card">
      <h5>{{ name }}</h5>
      <div class="item-header"></div>
      <p>{{ desc }}</p>
    </div>
    <p class="price">${{ price }}</p>
  </div>
</div>
</template>

<script>
export default {
  name: 'MenuItem',
  props: ['index', 'name', 'desc', 'price', 'imageName']
}
</script>

<style scoped>
.card-top-level {
  display: flex;
  margin-top: 10px;
  margin-right: 40px;
  background-color: white;
  max-width: 400px;
  padding: 0;
  min-height: 150px;
  box-shadow: 0px 0px 3px #888888;
}
.menu-card {
  padding: 10px;
}

.card-image {
  height: 150px;
  width: 150px;
}

.price {
  margin: 10px 10px auto auto;
}

.item-header {
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
  width: 50px;
  border-bottom: 1px solid #8a0707;
}
</style>



