<template>
<div>
  <h3 class="page-header">Menu</h3>
  <div class="row">
    <template v-for="(item, index) in menusItems">
      <app-menu-item
        :key="item.name"
        :index="index"
        :name="item.name"
        :desc="item.desc"
        :price="item.price"
        :imageName="item.imageName"></app-menu-item>
    </template>
  </div>
  <div class="disclaimer">
    <p>Before placing your order, please inform your server if a person in your party has a food allergy.</p>
    <p>Menu Items and prices subject to change.</p>
  </div>
</div>
</template>

<script>
import MenuItem from './MenuItem'
export default {
  components: {
    'app-menu-item': MenuItem
  },
  name: 'Menu',
  data: function () {
    return {
      menusItems: [
        {
          name: 'Escargot a la Bourguignon',
          desc: 'herbal garlic butter sauce, grilled ciabatta',
          price: 15
        },
        {
          name: 'Grilled Portobello Mushroom',
          desc: 'blue cheese, field greens salad',
          price: 12
        },
        {
          name: 'Chicken Teriyaki Dumplings',
          desc: '',
          price: 12
        },
        {
          name: 'Shrimp Dumplings',
          desc: '',
          price: 14
        },
        {
          name: 'Red Rooster Wings',
          desc: '',
          price: 14
        },
        {
          name: 'Fried Shrimp',
          desc: '',
          price: 14
        },
        {
          name: 'Red Rooster Salad',
          desc: 'organic field greens, tomatoes, cucumber, red onions, walnuts, goat cheese, raspberry vinaigrette',
          price: 10
        },
        {
          name: 'Shopska Salad',
          desc: 'tomatoes, cucumbers, roasted red peppers, red onions, parsley, shredded feta',
          price: 10
        },
        {
          name: 'Beets Salad',
          desc: 'organic field greens, roasted almonds, orange segment, citrus vinaigrette',
          price: 10
        }
      ]
    }
  }
}
</script>

<style scoped>
.disclaimer {
  text-align: center;
  margin-top: 40px
}

.menu-space {
  height: 200px;
}

.page-header {
  margin-bottom: 30px;
}
</style>



